@use '../custom' as *;

.btn-header {
    // BG, Border, Color de texto, Hover: BG, border, Texto
    @include button-variant($primary, transparent, $secondary, darken($primary, 10%), transparent, darken($secondary, 10%));
    @include button-size( 1rem, 5rem, 2rem, .5rem);
    font-weight: 700;
    text-decoration: none;
    text-align: center;
    display: block;
    margin-top: 1rem;

    @include media-breakpoint-up(md) {
        display: inline-block;
    }
}

.boton-plan {
    @extend .btn-header;
    border: 1px solid $white;
    display: flex;
    justify-content: center;
}